import atoms from './atoms';
import molecules from './molecules';

const all = [
    ...atoms,
    ...molecules
]

const pages = {
    atoms,
    molecules
}
const pagesNames = Object.keys(pages)

const templates = {
    getAll: () => all,
    getPageNames: () => pagesNames,
    getByPath: (str = '') => {
        if(!str) return [];
        const [page, component] = str.split('/')
        const pageFound = pagesNames.filter(elem => elem == page).shift()
        const componentFound = all.filter(elem => elem.name == component).shift()
        console.log(pageFound)
        console.log(componentFound)
        return 
    }
}

export default templates